#Clients {
  text-align: center;
  padding-top: 100vh;
  /*
  .global-text();
  font-size: 6vw;
  line-height: .78;
  letter-spacing: -.1rem;
  svg {
    .global-text();
    font-size: 6vw;
    line-height: .78;
    letter-spacing: -.1rem;
    margin-bottom: 1em;
    margin-top: 1em;
  }
  */
  svg {
    width: 85%;
  }
  text {
    opacity: 0;
    display: block;
  }
  p {
    .global-text();
    font-size: 6vw;
    line-height: .85;
    letter-spacing: -.15rem;
    margin-bottom: 1em;
    margin-top: 1em;
  }
  ul {
    margin: 0;
    padding: 0;
    padding-top: 1em;
    li {
      list-style: none;
      text-align: center;
    }
  }
}

#About {
  padding:10em 10px;
  span {
    .global-text();
    text-transform: none;
    font-size: 6vw;
    line-height: .9;
    letter-spacing: -.1rem;
  }
}

@animateSpeed: 1.6s;

#Clients li, #Clients text, #Clients p, #About span {
  opacity: 0;
  &.animate-in {
    .keyframes(fade-in;{
      0% { 
        opacity: 0; 
      }
      100% { 
        opacity: 1; 
      }
    });
    .animation(fade-in @animateSpeed cubic-bezier(0.4, .3, 0, 1) forwards);
    animation-delay: 0s;
  }
}

.in-view {
  opacity: 0;
  &.animate-in {
    .keyframes(fade-up;{
      0% { 
        opacity: 0; 
        transform: translateY(20px);
      }
      100% { 
        opacity: 1; 
        transform: translateY(0);
      }
    });
    .animation(fade-up @animateSpeed cubic-bezier(0.4, .3, 0, 1) forwards);
    animation-delay: 0s;
  }
}

/* SHOWCASE */
.projects {
  //margin-right: -6px;
  //margin-bottom: 50vh;
  margin-left: 5px;
  clear: left;
  &:after {
    content: '';
    clear: left;
    display: block;
  }
  @media @tablet {
    margin-top: 100px;
  }
}

.project {
  clear: left;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  @media @tablet {
    margin-bottom: 100px;
  }
  br {
    display: block;
    flex-basis: 100%;
    width: 100%;
    content: '';
  }
}
.project-meta {
  position: absolute;
  @media @tablet {
    position: relative;
    text-align: center;
    margin-bottom: 10px;
    margin-left: 10px;
    h2 {
      text-align: left;
    }
  }
}

.project-image {
  position: relative;
  align-self: flex-end;
  &:hover {
    .project-meta {
      display: block;
    }
  }
  .project-meta {
    position: absolute;
    white-space: nowrap;
    display: none;
    @media @tablet {
      position: relative;
      left: auto !important;
      right: auto !important;
      top: auto !important;
      bottom: auto !important;
      margin: 0;
      padding: 0;
      display: none;
    }
  }
  &.caption-br {
    .project-meta {
      left: 100%;
      bottom: .1rem;
    }
  }
  &.caption-bl {
    .project-meta {
      right: 100%;
      bottom: .1rem;
      text-align: right;
      padding-right: 5px;
    }
  }
  &.caption-tr {
    .project-meta {
      left: 100%;
      top: -.1rem;
    }
  }
  &.caption-al {
    .project-meta {
      bottom: 100%;
      left: 0.1rem;
    }
  }
  &.caption-ar {
    .project-meta {
      right: 10px;
      bottom: 100%;
      text-align: right;
    }
  }
  &.caption-bel {
    .project-meta {
      left: 0%;
      top: 100%;
    }
  }
  &.caption-ber {
    .project-meta {
      right: 0;
      top: 100%;
      padding-right: 5px;
      text-align: right;
    }
  }
  &.caption-cl {
    .project-meta {
      right: 100%;
      top: 50%;
      padding-right: 5px;
      text-align: right;
    }
  }
  &.caption-cr {
    .project-meta {
      left: 100%;
      top: 50%;
      
    }
  }
  &.caption-tl {
    .project-meta {
      right: 100%;
      top: -.1rem;
      text-align: right;
      padding-right: 5px;
    }
  }
  &.clear {
    clear: left;
  }
  img {
    width: 100%;
  }
}

.to-top {
  text-align: center;
  button {
    background: none;
    border: none;
    width: 100%;
    .global-text();
    padding-bottom: 15em;
    outline: none;
  }
}